@import "src/assets/styles/colors_fonts";

.homePage__top-container {
  @include flex-center-sb;
  width: 100%;
  background-color: $primary_blue;
  height: 600px; }

.homePage__top-block {
  @include flex-center-sa;
  max-width: 1400px;
  width: 100%;
  margin: 0 auto; }

.homePage__top-left {
  @include flex-columns;
  align-items: flex-start;
  color: $primary_light;
  width: 63%;
  h1 {
    max-width: 590px;
    font-size: 51px !important; }
  h3 {
    max-width: 510px;
    font-size: 28px !important; } }

.homePage__top-img {
  width: 80%;
  height: 45%;
  margin-left: 30px; }

.homePage__top-right {
  display: flex;
  justify-content: flex-end; }

.homePage__codeTabs-container {
  margin-top: 70px;
  color: black;
  display: flex;
  justify-content: space-between;
  width: 40%;
  div {
    cursor: pointer; } }


//code preview [Highlight] change styles
.homePage__top-left {
  .pgsql {
    padding: 60px !important;
    width: 600px;
    height: 200px;
    overflow-y: auto;
    word-break: break-word; } }

.homePage__right-description {
  padding-left: 50px; }

.homePage__middle-right {
  display: flex;
  justify-content: flex-start; }

.homePage__noActiveTab {
  background-color: $primary_blue;
  color: white;
  height: 35px;
  padding: 15px;
  @include flex-all-center;
  &:hover {
    text-decoration: underline white; } }

.homePage__activeTab {
  background-color: $primary_blue_dark;
  color: white;
  @extend .homePage__noActiveTab;
  &:hover {
    text-decoration: none; } }

.homePage__bottom-block {
  @extend .homePage__top-block;
  flex-direction: column;
  margin: 70px auto 140px auto; }


@media screen and (max-width: 500px) {
  .homePage__top-container {
    height: 590px; }
  .homePage__top-right {
    img {
      margin: 50px auto  0 auto; } }
  .faq__container_title-block {
    img {
      min-width: 25px !important;
      min-height: 25px !important;
      width: 25px !important;
      height: 25px !important; } }
  .price__table-line {
    width: calc(100% - 30px); }
  .price__table-cell-first {
    width: 18% !important;
    padding-left: 5px !important;
    p {
      font-size: 12px; }
    h4 {
      font-size: 13px !important; } }
  .price__table-cell {
    align-items: center !important;
    justify-content: center !important;
    width: 25% !important;
    p {
      font-size: 12px; }
    h4 {
      font-size: 18px !important; } }
  .products__block-cell {
    min-width: 35% !important; }
  .products__container-mobile {
    margin-top: 120px; }
  .products__block-cell {
    a {
      color: $primary_dark !important; } }
  .header__menu-title {
    a {
      color: white;
      text-decoration: none; } }
  .header__block-left {
    img {
      max-width: 40px;
      max-height: 40px;
      height: 40px;
      width: 40px;
      margin: 19px 0 5px 5px !important; }
    h1 {
      font-size: 45px !important;
      margin-top: 20px !important;
      margin-left: 10px !important;
      height: 40px; } }
  .pageGen_wrapper {
    padding: 40px 0 !important; }
  .homePage__right-description {
    padding: 30px !important; }
  .pageGen__code {
    padding: 10px 0 0 0 !important;
    pre {
      code {
        border-radius: 3px !important; }
      .pgsql {
        padding: 30px 20px !important; } }
    .hljs {
      max-width: 300px !important; } } }
