//main colors
$primary_blue: #0162d0;
$primary_blue_dark: #0129b5;
$primary_light: #f0faff;
$primary_dark: #121417;


//main fonts
@mixin primary_font-p {
  font-family: Ubuntu, sans-serif;
  font-size: 14px;
  font-weight: 600; }

@mixin primary_font-h1 {
  font-family: Ubuntu, sans-serif;
  font-size: 25px;
  font-weight: 800; }

@mixin primary_font-menu {
  font-family: Ubuntu, sans-serif;
  font-size: 16px;
  font-weight: 600; }

//flex
@mixin flex-all-center {
  display: flex;
  justify-content: center;
  align-items: center; }

@mixin flex-center-sb {
  display: flex;
  justify-content: space-between;
  align-items: center; }

@mixin flex-center-sa {
  display: flex;
  justify-content: space-around;
  align-items: center; }

@mixin flex-columns {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center; }


@mixin block-shadow {
  box-shadow: 1px 1px 4px 1px rgba(0,0,0,0.33);
  -webkit-box-shadow: 1px 1px 4px 1px rgba(0,0,0,0.33);
  -moz-box-shadow: 1px 1px 4px 1px rgba(0,0,0,0.33); }

.buttons_block {
  width: 40%;
  @include flex-center-sb; }

.buttons_block-bottom {
  width: 60%;
  @include flex-center-sb;
  a {
    width: calc(45% - 2px);
    button {
      width: 100%; } } }

.white-btn {
  min-width: 45%;
  min-height: 40px;
  padding: 5px 15px;
  color: $primary_blue;
  background-color: $primary_light;
  border: none;
  border-radius: 3px;
  outline: none;
  cursor: pointer;
  font-size: 14px;
  &:hover {
    text-decoration: underline $primary_blue; } }

.white-btn-border {
  min-width: calc(45% - 2px);
  min-height: calc(40px - 2px);
  padding: 5px;
  color: $primary_blue;
  background-color: $primary_light;
  border-radius: 3px;
  outline: none;
  cursor: pointer;
  font-size: 14px;
  border: 1px solid $primary_blue;
  &:hover {
    text-decoration: underline $primary_blue; } }

.blue-btn {
  min-width: 45%;
  min-height: 40.4px;
  padding: 5px 15px;
  color:  $primary_light;
  background-color: $primary_blue_dark;
  border: none;
  border-radius: 3px;
  outline: none;
  cursor: pointer;
  font-size: 14px;
  &:hover {
    text-decoration: underline $primary_light; } }




@media screen and (max-width: 500px) {
  .buttons_block {
    width: 70%; }
  .blue-btn {
    //width: 98% !important
    min-width: 98% !important;
    height: 20px !important;
    line-height: 13px; }
  .white-btn {
    //width: 98% !important
    height: 19.5px !important;
    line-height: 13px; }

  .homePage__top-block, .homePage__bottom-block {
    flex-wrap: wrap; }

  .homePage__top-left h3 {
    max-width: 539px; }

  .homePage__top-left {
    width: 90%; }

  .homePage__top-left {
    .pgsql {
      padding: 20px !important;
      width: 300px;
      height: 200px;
      overflow: auto;
      word-break: break-word; } }

  .products-dropdown {
    display: none; }
  .homePage__top-left {
    h1 {
      font-size: 30px !important; }
    h3 {
      font-size: 20px !important; } }

  .homePage__noActiveTab {
    padding: 12.6px; }
  .homePage__activeTab {
    padding: 12.6px; }

  div.homePage__top-block:nth-child(2) > div:nth-child(1)homePage__codeTabs-container {
    margin: 0 auto !important;
    display: flex;
    justify-content: center;
    align-items: center; }


  div.homePage__top-block:nth-child(2) > div:nth-child(1) {
    display: flex;
    justify-content: center;
    align-content: center;
    align-items: center; }
  .homePage__codeTabs-container {
    width: 100% !important; } }
