@import "src/assets/styles/colors_fonts";

.faq__container {
  @include flex-center-sb;
  color: black;
  max-width: 1400px;
  width: 100%;
  margin: 50px auto;
  height: 100%;
  flex-wrap: wrap;
  flex-direction: column; }

.faq__container-head {
  width: 90%;
  max-width: 1000px;
  margin: 0 auto; }

.faq__container_block {
  display: flex;
  flex-direction: column;
  width: 70%; }

.faq__container_title-block {
  display: flex;
  align-items: center;
  justify-content: flex-start;

  img {
    width: 35px;
    height: 35px;
    margin-right: 10px;
    cursor: pointer; }
  h4 {
    cursor: pointer; } }

.faq__container_content {
  margin-top: -15px;
  width: 90% !important;
  max-width: 1000px;
  display: flex; }

.faq__container_content-hide {
  display: none; }
